@font-face {
  font-family: 'TTTogether';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/TTTogetherA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
.allcontainer{
  scroll-snap-type: y mandatory;
}
.container{
    display:flex;
    font-size: 1.5em;
    flex-direction: row;
    padding-left:10%;
    scroll-snap-align: center;
}
.spin{
    background-color:red;
    width:20%;
}
.images{
  width:90%;
  margin-top:-50px;
}
.textpart1{
  width:50%;
}
.imagepart1{
  width:50%;
}
.pin{
  width:190px;
}
.titleMain{
  color:white;
  font-family: TTTogether;
  font-size: 3.2em;
  margin-top:120px;
}
.titleItems{
  color:black;
  font-family: TTTogether;
  font-size: 1.0em;
  margin-top:-60px;
}
.titleWe{
  color:white;
  font-family: TTTogether;
  font-size: 2.3em;
  margin-top:120px;
  background:linear-gradient(to right, rgb(255, 138, 60) 30%, rgba(250, 250, 250, 0.363));
  padding:5px;
  border-radius:10px;
}
.Button{
  width:230px;
  height:90px;
  background-color:white;
  border-radius: 45px;
  border: 1px solid #47b9ff;
  box-shadow: 5px 5px 10px rgba(25,25,25,0.4);
  display:flex;
  justify-content:center;
  align-items: center;
  color:#47b9ff;
  font-size:1em;
  font-family: TTTogether;
  font-weight:200;
  position:absolute;
  bottom:30px;
  left:49%;
}

.secondBlock{
  display:flex;
  scroll-snap-align: center;
  width:100%;
  background-color:white;
  flex-direction: row;
}
.textpart{
  width:30%;
  padding-top:8%;
  padding-left:10%;
}
.imagepart{
  width:70%;
}
.backImage{
  width:100%;
  margin-left:0%;
}
.titleMain2{
  color:#47b9ff;
  font-family: TTTogether;
  font-size: 2.7em;
}
.titleDesc{
  color:rgb(90, 90, 90);
  font-family: TTTogether;
  margin-top:-10px;
  font-size: 1.3em;
}
.desc{
  color:black;
  font-family: TTTogether;
  font-size: 1.8em;
  margin-top:70px;
}
.apply{
  height:90px;
  width:300px;
  background-color:#47b9ff;
  border-radius: 40px;
  border: 1px solid #47b9ff;
  box-shadow: 5px 5px 10px rgba(25,25,25,0.4);
  display:flex;
  justify-content:center;
  align-items: center;
  color:white;
  font-size:1.8em;
  font-family: TTTogether;
  font-weight:200;
  margin-top:10px;
}
.logoimage{
  width:50px;
  height:50px;
  border-radius:15px;
  margin-right:10px;
}
.logo{
  flex-direction:row;
  display: flex;
  align-items: center;
  margin-top:-30px;
  padding-left:5%;
}
.logodesc{
  color:black;
  /* font-family: TTTogether; */
  font-size: 2em;
  margin-bottom:9%;
}
.input{
  height:40px;
  width:90%;
}

@media (prefers-reduced-motion: no-preference) {
    .spin {
        animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  